import React from "react";
import { connect } from "react-redux";
import {
  Tabs,
  Tab,
  Paper,
  createMuiTheme,
  ThemeProvider,
} from "@material-ui/core";
import { orange, blue, grey } from "@material-ui/core/colors";
import {
  NotificationsActiveOutlined,
  SentimentDissatisfiedOutlined,
  NotesOutlined,
} from "@material-ui/icons";

const theme = createMuiTheme({
  palette: {
    primary: blue,
    secondary: orange,
  },
});
class ForComp extends React.Component {
  state = { value: 0 };

  componentDidMount() {
    if (this.props.location.pathname.toLowerCase().includes("sensitive")) {
      this.setState({ value: 1 });
    }
  }
  render() {
    return (
      <ThemeProvider theme={theme}>
        <Paper style={{ backgroundColor: "rgba(0,0,0,0.01)" }} elevation={0}>
          <Tabs
            onChange={(event, value) => {
              this.setState({ value });
              this.props.history.push(
                `${
                  value == 0
                    ? "/anonymity/nearby"
                    : "/anonymity/nearby/sensitive"
                }`
              );
            }}
            TabIndicatorProps={{
              style: { height: 2, backgroundColor: "black" },
            }}
            centered
            value={this.state.value}
          >
            <Tab
              // style={{ marginRight: "2%" }}
              color="primary"
              textColor="primary"
              icon={
                <img
                  src="https://img.icons8.com/color/48/000000/peace-symbol.png"
                  style={{ width: 30, height: 30 }}
                />
              }
              label="insensitive"
              disableRipple
            ></Tab>
            <Tab
              icon={
                <img
                  src="https://img.icons8.com/color/48/000000/keep-away-from-children.png"
                  style={{ width: 30, height: 30 }}
                />
              }
              label="Sensitive"
              disableRipple
            ></Tab>
          </Tabs>
        </Paper>
      </ThemeProvider>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    currentHomeTab: state.HomeReducer,
  };
};

export default connect(mapStateToProps)(ForComp);
